/**
 * TypeScript interfaces for working with SharePoint REST APIs
 * @remarks
 * This package defines TypeScript interfaces that describe various ODATA objects
 * returned by SharePoint REST APIs.  It is useful when implementing data layers that
 * interpret these types.  This package has no performance cost because it does not
 * contain any runtime code; it consists only of type definitions.
 */
declare const packageDescription: void; // tslint:disable-line:no-unused-variable

export {
  IODataNavigationNode,
  IODataNavigationNodeCollection } from './IODataNavigationNodeCollection';
export { default as IODataWeb } from './IODataWeb';
export { IODataChangeToken, IODataContextWebInformation } from './SmallTypes';
export { default as IODataList } from './IODataList';
export { default as IODataListItem } from './IODataListItem';
export { default as IODataUser } from './IODataUser';
